<template>
    <div>
        <LineChartGenerator :chart-options="chartOptions" :chart-data="dataList" :chart-id="chartId"
            :dataset-id-key="datasetIdKey" :plugins="plugins" :css-classes="cssClasses" :styles="styles" :width="width"
            :height="height" />
        <Bar :chart-options="chartOptions" :chart-data="dataList" :chart-id="chartId" :dataset-id-key="datasetIdKey"
            :plugins="plugins" :css-classes="cssClasses" :styles="styles" :width="width" :height="height" />
    </div>
</template>
<script>
import { Line as LineChartGenerator } from 'vue-chartjs/legacy'
import { Bar } from 'vue-chartjs/legacy'
import {
    Chart as ChartJS,
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    CategoryScale,
    BarElement,
    PointElement
} from 'chart.js'

ChartJS.register(
    Title,
    Tooltip,
    Legend,
    LineElement,
    LinearScale,
    CategoryScale,
    PointElement,
    BarElement
)
export default {
    name: 'LineChart',
    components: {
        LineChartGenerator,Bar
    },
    props: {
        chartId: {
            type: String,
            default: 'line-chart'
        },
        datasetIdKey: {
            type: String,
            default: 'label'
        },
        width: {
            type: Number,
            default: 400
        },
        height: {
            type: Number,
            default: 400
        },
        cssClasses: {
            default: '',
            type: String
        },
        styles: {
            type: Object,
            default: () => { }
        },
        plugins: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            chartOptions: {
                responsive: true,
                maintainAspectRatio: false
            },
            dataList: {
                labels: [
                    
                ],
                datasets: [
                    {
                        label: 'Data Adjustment',
                        backgroundColor: '#5e6ca6',
                        data: []
                    }
                ]
            }
        }
    },
    created() {
        this.initialize()
    },
    methods: {
        initialize() {
            this.dataList.labels = this.$store.getters.getData.map((el) => {
                return el.month
            })
            this.dataList.datasets[0].data = this.$store.getters.getData.map((el) => {
                return el.adjust
            })
            console.log('this.dataList', this.dataList);
        }
    },
}
</script>