import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    dataList:[]
  },
  getters: {
    getData (state) {
      return state.dataList
    }
  },
  mutations: {
    SET_DATA(state,payload){
      state.dataList = payload
    }
  },
  actions: {
  },
  modules: {
  }
})
