<template>
    <div>
        <v-text-field v-model="edit" type="number" @change="edited" class="textRight">
            <v-icon slot="append" color="blue">
                mdi-percent
            </v-icon>
        </v-text-field>
    </div>

</template>
<script>
import axios from 'axios'
export default {
    props: ["data"],
    data() {
        return {
            edit: ''
        }
    },
    created() {
        // props are exposed on `this`
        this.edit = this.data
    },
    methods: {
        async edited() {
            this.$emit('edited', true)
            try {
                await axios.post('https://jsonplaceholder.typicode.com/posts', this.edit).then((el) => {
                    console.log('Updated', el);
                    this.$emit('edited', false)
                })
            } catch {
                this.$emit('edited', false)
                alert("Ошибка");
            }
            finally {
                this.$emit('edited', false)
            }
        }
    },
}
</script>
<style lang="scss">
.v-text-field__slot{
    input{
        text-align: right;
    }
}
</style>