<template>
    <!-- hide-default-footer
  disable-pagination -->
    <v-data-table dense :headers="headers" :loading="loading" :items="datalist" class="elevation-1">
        <template v-slot:[`item.month`]="{ item }">
            <v-chip>
                {{item.month}}
            </v-chip>
        </template>
        <template v-slot:[`item.adjust`]="{ item }">
            <AdjustEdit :data="item.adjust" @edited="edited" />
        </template>
        <template v-slot:[`item.date`]="{ item }">
            <v-chip :color="item.date > 0 ? 'green' : 'red'" text-color="white">
                {{item.date > 0 ? '+' : ''}}{{item.date + '%'}}
            </v-chip>
        </template>
        <template v-slot:[`item.actions`]="{ item }">
            <v-icon @click="zeroPoint(item)" :color="isSelected(item)">
                mdi-numeric-0-circle
            </v-icon>
        </template>
    </v-data-table>
</template>
<script>
import AdjustEdit from './AdjustEdit.vue';

export default {
    components: {
        AdjustEdit,
    },
    data: () => ({
        loading: false,
        headers: [
            {
                text: 'Month',
                align: 'center',
                sortable: false,
                value: 'month',
                width: '25%'
            },
            { text: 'Adjustment Delta', value: 'adjust', width: '30%', align: 'center', sortable: false, },
            { text: 'Adjusted to Date', value: 'date', width: '25%', align: 'center', sortable: false },
            { text: 'Starting point', value: 'actions', width: '25%', align: 'center', sortable: false },
        ],
        datalist: [],
        startId: null,
        min: -35,
        max: 35
    }),


    created() {
        this.initialize()
    },

    methods: {
        zeroPoint(data) {
            this.startId = data.id
            this.generateData()
            this.datalist[data.id].date = 0
        },
        isSelected() {
            return 'blue darken-2'
        },
        initialize() {
            this.startId = null
            this.generateData()
        },
        random(min, max) {
            return Math.random() * (max - min) + min
        },
        generateData() {
            this.datalist = []
            const arrdate = this.generateDataList()
            for (const property in arrdate) {
                const data = {
                    month: arrdate[property],
                    adjust: this.random(-10, 11).toFixed(1),
                    date: this.randomDateAdjust(property),
                    id: property
                }
                this.datalist.push(data)
            }
            this.$store.commit("SET_DATA", this.datalist)
        },
        randomDateAdjust() {
            if (this.startId == null) {
                return this.random(-35, 35).toFixed(1)
            } else {
                // if(property < this.startId){
                //     this.min = this.max + this.min
                // }
                return this.random(-35, 35).toFixed(1)
            }
        },
        edited(data) {
            this.loading = data
        },
        generateDataList() {
            var daylist = this.getDaysArray(new Date("2007-07-01"), new Date());
            return daylist.map((v) => v.toISOString().slice(0, 10)) //).join(",")
        },
        getDaysArray(start, end) {
            for (var arr = [], dt = new Date(start); dt <= new Date(end); dt.setDate(dt.getDate() + 1)) {
                if (dt.getDate() == '1') {
                    arr.push(new Date(dt));
                }
            }
            return arr;
        },



    },
}
</script>